
.data__card {
    display: flex;
    flex-direction: column;
    width: 20.75em;
    min-height: 20.75em;
    overflow: hidden;
    border-radius: 0.5em;
    text-decoration: none;
    background: white;
    margin: 1em;
    padding: 2.75em 2.5em;
    box-shadow: 0 1.5em 2.5em -0.5em rgba(0, 0, 0, .1);
    transition: transform 0.45s ease, background 0.45s ease;
}
.data__card h3 {
    color: #2e3c40;
    font-size: 3.5em;
    font-weight: 600;
    line-height: 1;
    padding-bottom: 0.5em;
    margin: 0 0 0.142857143em;
    border-bottom: 2px solid #45a29e;
    transition: color 0.45s ease, border 0.45s ease;
}
.data__card h4 {
    color: #627084;
    text-transform: uppercase;
    font-size: 1.125em;
    font-weight: 700;
    line-height: 1;
    letter-spacing: 0.1em;
    margin: 0 0 1.777777778em;
    transition: color 0.45s ease;
}
.data__card p {
    opacity: 0.8;
    color: #627084;
    font-weight: 600;
    line-height: 1.8;
    margin: 0 0 1.25em;
    transform: translateY(-1em);
    transition: opacity 0.45s ease, transform 0.5s ease;
}

.btn__div
{
    margin-top: 30px;
    position: sticky;
    text-align: center;
    bottom: 0;
}

.register
{
    color: #45a29e;
}

.data__card:hover {
    background: #232323;
    transform: scale(1.02);
}
.data__card:hover h3 {
    color: #fff;
    border-bottom-color: #66fcf1;
}
.data__card:hover h4 {
    color: #fff;
}
.data__card:hover p {
    opacity: 1;
    color: white;
    transform: none;
}
.data__card:hover .register{
    color: #66fcf1;
}

@media only screen and (max-width: 600px) {
    
    .data__card
    {
        width: 18em;
        min-height: 20.75em;
        padding: 1.25em 1em;

    }
  }

@keyframes point {
    0% {
        transform: translateX(0);
   }
    100% {
        transform: translateX(0.125em);
   }
}
